.bg_container {
  display: flex;
  flex-direction: column;
  background-color: #000000;
  background-size: cover;
}

.jobDetailsView_container {
  display: flex;
  flex-direction: column;
  background-color: #272727;
  background-size: cover;
  border-radius: 20px;
  flex-shrink: 1;
  padding: 20px;
  margin-top: 40px;
  margin-left: 20px;
  margin-right: 20px;
}
@media screen and (min-width: 768px) {
  .jobDetailsView_container {
    padding: 40px;
    margin-top: 80px;
    margin-left: 50px;
    margin-right: 50px;
  }
}

.companyDetails_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.company_Logo {
  width: 65px;
  height: 65px;
}

.jobTitle_rating_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 15px;
}

.jobTitle {
  font-family: 'Roboto';
  font-size: 20px;
  font-weight: 500;
  color: #ffffff;
  margin-bottom: 0px;
  padding-bottom: 0px;
}
@media screen and (min-width: 768px) {
  .jobTitle {
    font-size: 24px;
  }
}

.rating_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-top: 0px;
}

.rating_icons {
  font-size: 20px;
  color: #fbbf24;
  margin-top: 0px;
  padding-top: 0px;
}
@media screen and (min-width: 768px) {
    .rating_icons {
      font-size: 24px;
    }
  }

.rating_text {
  font-family: 'Roboto';
  font-size: 18px;
  font-weight: 500;
  color: #ffffff;
  margin-left: 5px;
}
@media screen and (min-width: 768px) {
    .rating_text {
      font-size: 20px;
    }
  }

.location_empType_package_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.location_empType {
  display: flex;
  flex-direction: row;
}

.location {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-right: 25px;
}

.empType {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.job_icons {
  font-size: 20px;
  color: #ffffff;
  margin-right: 10px;
}
@media screen and (min-width: 768px) {
    .job_icons {
      font-size: 26px;
    }
  }

.job_icons_text {
  font-family: 'Roboto';
  font-size: 14px;
  color: #ffffff;
}
@media screen and (min-width: 768px) {
    .job_icons_text {
      font-size: 15px;
    }
  }

.package {
  display: flex;
}

.package_text {
  font-family: 'Roboto';
  font-size: 18px;
  font-weight: 500;
  color: #ffffff;
}
@media screen and (min-width: 768px) {
    .package_text {
      font-size: 22px;
      margin: 0px;
    }
  }

.line {
  width: 100%;
  border: 1px solid #64748b;
}

.description_container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 0px;
}

.description {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.description_heading {
  font-family: 'Roboto';
  font-size: 18px;
  font-weight: 500;
  color: #ffffff;
}
@media screen and (min-width: 768px) {
    .description_heading {
      font-size: 30px;
    }
  }

.visit_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.visit_Link {
  font-family: "Roboto";
  font-size: 20px;
  font-weight: 500;
  color: #6366f1;
  text-decoration: none;
  margin-right: 8px;
}
@media screen and (min-width: 768px) {
  .visit_Link {
    font-size: 24px;
  }
}

.visit_icon {
  font-size: 20px;
  font-weight: bold;
  color: #6366f1;
}
@media screen and (min-width: 768px) {
    .visit_icon {
      font-size: 24px;
    }
  }

.description_text {
  font-family: 'Roboto';
  font-size: 16px;
  line-height: 1.8;
  color: #ffffff;
  margin-top: 0px;
  padding-top: 10px;
}
@media screen and (min-width: 768px) {
    .description_text {
      font-size: 20px;
    }
  }

  .skills_heading {
    font-family: 'Roboto';
    font-size: 18px;
    font-weight: 500;
    color: #ffffff;
  }
  @media screen and (min-width: 768px) {
      .skills_heading {
        font-size: 30px;
      }
    }

.skills_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 0px;
}

.skills {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  min-width: 300px;
  max-width: 350px;
  padding: 25px;
}

.skill_image {
  width: 60px;
  height: 60px;
  margin-right: 15px;
}

.skill_name {
  font-family: "Roboto";
  font-size: 18px;
  color: #ffffff;
}

.lifeAtCompany_heading {
    font-family: 'Roboto';
    font-size: 18px;
    font-weight: 500;
    color: #ffffff;
  }
  @media screen and (min-width: 768px) {
      .lifeAtCompany_heading {
        font-size: 30px;
        margin-top: 50px;
      }
    }

.lifeAtCompany_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.lifeAtCompany_description {
    font-family: 'Roboto';
    font-size: 16px;
    line-height: 1.8;
    color: #ffffff;
    margin-top: 0px;
    padding-top: 10px;
  }
  @media screen and (min-width: 768px) {
      .lifeAtCompany_description {
        font-size: 20px;
        width: 50%;
        margin-right: 20px;
      }
    }

.lifeAtCompany_image {
  width: 40%;
}

.similarJobs_container {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    margin-right: 20px;
  }
  @media screen and (min-width: 768px) {
    .similarJobs_container {
      margin-top: 20px;
      margin-left: 50px;
      margin-right: 50px;
    }
  }

.similarJob_heading {
    font-family: 'Roboto';
    font-size: 18px;
    font-weight: 500;
    color: #ffffff;
  }
  @media screen and (min-width: 768px) {
      .similarJob_heading {
        font-size: 30px;
      }
    }

.similar_card {
  display: flex;
  flex-direction: row;
  margin: 0px;
  padding: 0px;
}

